@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

// project specific CSS goes here
html {
  scroll-behavior: smooth;
}

[x-cloak] {
  display: none;
}

body {
  @apply relative flex flex-col min-h-screen w-screen overflow-y-auto overflow-x-hidden bg-light font-poppins text-dark scrollbar-thin scrollbar-track-dark scrollbar-thumb-primary dark:bg-dark dark:text-light dark:scrollbar-track-primary dark:scrollbar-thumb-dark bg-fixed bg-cover bg-no-repeat bg-center;
}

.gradient {
  @apply bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500;
}

.gradient-btn {
  @apply bg-gradient-to-tr from-[#FFB147] via-[#FF6C63] to-[#B86ADF];
}

.gradient-b {
  @apply bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500 pb-1;
}

.gradient-l {
  @apply bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500 pl-1;
}

.gradient-r {
  @apply bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500 pr-1;
}

.gradient-t {
  @apply bg-gradient-to-r from-pink-500 via-red-500 to-yellow-500 pt-1;
}
